.card {
    position: relative;
    cursor: pointer;
}

img {
    width:10rem;
    height: 10rem;
    display: block;
    border: 2px solid whitesmoke;
    border-radius: 6px;
}

/* front of the card */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front {
    transform: rotateY(0deg);
    transition-delay:0.2s;
}

/* back of the card */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay:0.2s;
}

.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}